import { notFound } from 'next/navigation';
import Image from 'next/image';
import { Navbar } from '@/components/navbar';
import { Footer } from '@/components/footer';
import { useAuction } from '@/hooks/use-auctions';
import { AuctionStatusBadge } from '@/components/bid-status-badge';
import { CountdownInline } from '@/components/countdown-timer';
import { formatKAS } from '@/lib/mock-data';
import { cn } from '@/lib/utils';

export default function AuctionDetailPage({ params }: { params: { id: string } }) {
  const { auction, loading } = useAuction(params.id);

  if (!loading && !auction) {
    notFound();
  }

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <p className="text-muted-foreground">Loading auction...</p>
      </div>
    );
  }

  const isEnded = auction!.status === 'ended';

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />
      <main className="flex-1 max-w-4xl mx-auto p-4 space-y-6">
        {/* Image */}
        <div className="relative aspect-square w-full rounded-xl overflow-hidden bg-muted">
          <Image
            src={auction!.imageUrl || '/placeholder.svg'}
            alt={auction!.title}
            fill
            className={cn('object-cover', isEnded && 'grayscale')}
          />
        </div>

        {/* Header */}
        <div className="flex items-start justify-between">
          <h1 className="text-3xl font-bold text-foreground">{auction!.title}</h1>
          <AuctionStatusBadge status={auction!.status} />
        </div>

        {/* Seller */}
        <p className="text-sm text-muted-foreground">
          Seller: {auction!.seller.name || 'Anonymous'} ({auction!.seller.address})
        </p>

        {/* Description */}
        <p className="text-foreground">{auction!.description}</p>

        {/* Pricing & Timer */}
        <div className="flex items-center gap-8 mt-4">
          <div>
            <p className="text-xs uppercase text-muted-foreground">
              {isEnded ? 'Final Price' : 'Current Bid'}
            </p>
            <p className="text-2xl font-bold text-foreground tabular-nums">
              {formatKAS(auction!.currentPrice)} <span className="text-sm font-medium text-muted-foreground">KAS</span>
            </p>
          </div>
          <div>
            <p className="text-xs uppercase text-muted-foreground">
              {isEnded ? 'Ended' : 'Time Left'}
            </p>
            {isEnded ? (
              <span className="text-sm text-muted-foreground">Auction closed</span>
            ) : (
              <CountdownInline endTime={auction!.endTime} className="text-sm" />
            )}
          </div>
        </div>

        {/* Bids */}
        <section className="mt-8">
          <h2 className="text-xl font-semibold text-foreground mb-4">Bid History</h2>
          {auction!.bids.length === 0 ? (
            <p className="text-muted-foreground">No bids yet.</p>
          ) : (
            <ul className="space-y-2">
              {auction!.bids.map((bid) => (
                <li key={bid.id} className="flex justify-between items-center p-2 border rounded">
                  <span className="font-medium">{formatKAS(bid.amount)} KAS</span>
                  <span className="text-sm text-muted-foreground">{new Date(bid.timestamp).toLocaleString()}</span>
                </li>
              ))}
            </ul>
          )}
        </section>
      </main>
      <Footer />
    </div>
  );
}
